@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;800&display=swap);
body{font-family:"Montserrat",sans-serif;background:#151A30;margin:0}

.mainHeader{background:#222B45;padding:12px;height:72px;box-sizing:border-box;display:flex;align-items:center;justify-content:space-between}.mainHeader-logo{display:flex;height:48px;width:150px;align-items:center}.mainHeader-logo--img{width:48px;height:48px;background:#36f;border-radius:3px;box-sizing:border-box;margin-right:18px}.mainHeader-logo--txt{color:#fff;font:800 24px/30px "Montserrat",sans-serif;display:inline-block;vertical-align:middle}.mainHeader-logIn--txt{color:#8F9BB3;margin-right:32px;font:500 14px/24px "Montserrat",sans-serif}.mainHeader-logIn--btn{width:98px;display:inline-block}

.button{display:inline-block;height:48px;font:500 14px/24px "Montserrat",sans-serif;text-decoration:none;outline:none;cursor:pointer;border-radius:3px;box-sizing:border-box;color:#fff;border:1px solid transparent;width:inherit}.button--blue{background:#36f}.button--blue:hover{background:#598CFF}.button--blue:active{background:#244EDB}.button--green{background:#40C04C}.button--green:hover{background:#6AD86C}.button--green:active{background:#2DA545}.button--dark{background:#1E2743;color:#8F9BB3}.button--dark:hover{background:#343C55}.button--dark:active{background:#151A30}.button--disabled,.button--disabled:hover{background:#e3e7ed;color:#b8c1d0}

